        h1 Segmentacin del mercado

      .row.justify-content-center.align-items-center.mb-5(data-aos="fade-left")
        .col-lg-4.col-8.col-sm-6
          img(src="@/assets/curso/temas/tema2/img01.png", alt="")
          .py-2
        .col-lg-8 
          p De acuerdo con el concepto ms bsico, el segmentar se refiere a Cortar o partir algo en segmentos (RAE, 2020). En el caso del #[em marketing], consiste en dividir un mercado en varios grupos de individuos con caractersticas similares. La segmentacin, es una herramienta que permite a los estrategas del mercadeo y la comunicacin, conocer en detalle a grupos objetivo de individuos que presentan atributos particulares para el consumo de productos o el acceso a servicios especficos. 
          .cajon.color-secundario.p-4.mb-4
            p #[b Por su parte, Fernndez (2009), afirma:]
            p #[b La segmentacin de mercados es una herramienta de la mercadotecnia que requiere del apoyo de disciplinas como la estadstica, la psicologa, la sociologa y la geografa, entre otras y es a partir de una serie de variables que la segmentacin determina las caractersticas que hacen nico a un grupo de mercado.]
        
        .py-4
        figure.mb-5
          .video
            iframe(width="560" height="315" src="https://www.youtube.com/embed/kzaxGqbo3-s" title="YouTube video player" frameborder="0" allow="accelerometer; autoplay; clipboard-write; encrypted-media; gyroscope; picture-in-picture" allowfullscreen)
          figcaption Video: Segmentacin de mercados

        p Para el diseo de las estrategias de #[em marketing], el mercado se puede segmentar en dos grandes grupos: consumidores e industrial; estos, de igual forma, deben ser divididos en nichos ms pequeos. El primero de ellos, como su nombre lo indica, el grupo de consumidores: tiene que ver con las personas que son usuarias de los servicios o que consumen los productos. El segundo: se refiere a las empresas que conforman nichos de mercado industriales. 

      Separador
      #t_2_1.titulo-segundo.color-acento-contenido
        h2 2.1 Concepto de mercado
      
      .row.justify-content-center.align-items-center.mb-5(data-aos="fade-left")
        .col-lg-8
          .bloque-texto-b.color-secundario.p-4
            .bloque-texto-b__texto
        
              p De acuerdo con Patricio Bonta y Mario Farber (2002), en su libro 199 Preguntas Sobre #[em Marketing] y Publicidad, el mercado es:<br><br>
              p Donde confluyen la oferta y la demanda. En un sentido menos amplio, el mercado es el conjunto de todos los compradores reales y potenciales de un producto. Por ejemplo: el mercado de los autos est formado no solamente por aquellos que poseen un automvil, sino tambin por quienes estaran dispuestos a comprarlo y disponen de los medios para pagar su precio. (pg. 19)<br><br>
              h5 - Patricio Bonta y Mario Farber (2002)
            
        .col-lg-4.col-8.col-sm-6
          img(src="@/assets/curso/temas/tema2/img02.png", alt="")
      .py-3 
        .row.justify-content-center.align-items-center.mb-5(data-aos="fade-left")
          .col-lg-12
            .bloque-texto-f.color-secundario.pt-4.px-5.mb-5
              .bloque-texto-f__comillas
           
           
              p.text-regular.bloque-texto-f__texto Por su parte, Philip Kotler, Gary Armstrong, Dionisio Cmara e Ignacio Cruz (2012), en su libro #[em Marketing], indican que el mercado es un Conjunto de compradores reales y potenciales de un producto. Estos compradores comparten una necesidad o un deseo particular que puede satisfacerse mediante una relacin de intercambio. (pg.10)
              h5.text-regular.bloque-texto-f__texto #[b - Philip Kotler, Gary Armstrong, Dionisio Cmara e Ignacio Cruz  (2012)]
        .row.justify-content-center.align-items-center.mb-5
          .col-lg-5.col-8.col-sm-6.py-2
            img(src="@/assets/curso/temas/tema2/img03.png", alt="")
          .col-lg-7
            p En un sentido ms amplio de la palabra mercado y desde el punto de vista de la empresa, se podra decir que es el conjunto de todas las variables que interfieren en el desarrollo comercial de los productos y servicios; teniendo en cuenta que confluyen todos los actores del proceso de mercadeo y los factores que obligan a las empresas a desarrollar diferentes estrategias para permanecer en l y ser competitivas.

      Separador
      #t_2_2.titulo-segundo.color-acento-contenido
        h2 2.2 Proceso de segmentacin

      p De acuerdo con las teoras de la segmentacin, existen varios tipos: geogrfica, demogrfica, comportamental, psicogrfica, por uso del producto, por comportamiento de compra, entre otras. En el proceso que se relaciona a continuacin, se presentan los pasos esenciales para la segmentacin de mercados industriales y de consumidores, de forma integral; esto, con el fin de que quien desarrolle las estrategias, tenga la capacidad de aplicarlos en su totalidad o por partes de acuerdo con sus exigencias:
      
      .row.justify-content-start.mb-4(data-aos="zoom-in-left")
          .col-sm-12.col-lg-7
            .d-flex.align-items-center.mt-4
              img.w-card-icon(src="@/assets/curso/temas/tema2/1.png")
              h3.bg13.py-2.w-card-text Segmentacin industrial

      .row.justify-content-center.align-items-center.mb-5
        .col-lg-3.col-8.col-sm-6.py-2
          img(src="@/assets/curso/temas/tema2/img04.png", alt="")
        .col-lg-9
          p Muchas empresas tienen dentro de su portafolio productos o servicios, los cuales tienen como mercado objetivo otras empresas y su mtodo de negociacin incluye el B2B (#[em Business to Business]) o negocio a negocio. Por ejemplo, para un fabricante de mquinas de coser cuero, su mercado objetivo son fbricas de artculos de este material y no enfoca sus estrategias en el de consumidores.
          .row.justify-content-start.mb-4(data-aos="zoom-in-left")
            .col-sm-12.col-lg-9
              .d-flex.align-items-center.mt-4
                img.w-card-icon(src="@/assets/curso/temas/tema2/2.png")
                h3.bg13.py-2.w-card-text Pasos para la segmentacin industrial
              .py-2
              p El proceso de la segmentacin industrial es relativamente sencillo y la caracterizacin de las empresas incluye solo algunas variables muy puntuales. Los pasos para su aplicacin son:

      .row.justify-content-center.align-items-center.mb-5
        .col-lg-8
          h4 Seleccin del universo
          p Uno de los tipos de segmentacin ms importantes es la geogrfica. Este primer paso, permite delimitar el rea de cobertura que tendr el producto o servicio desde el punto de vista geogrfico. El universo, es toda la zona donde hacen presencia los clientes y la empresa est en capacidad de llegar all; por ejemplo, si una compaa vende harina industrial y sus clientes son fabricantes de alimentos, estos pueden estar en todo el pas y hacer entregas del producto por un servicio de courier. En ese caso, el universo es Colombia, sin importar que su fabricacin y distribucin est en el Valle del Cauca.
        .col-lg-4.col-8.col-sm-6
          img(src="@/assets/curso/temas/tema2/img05.png", alt="")
      .row
        .col-lg-10
          h4 Caracterizacin del mercado
          p La caracterizacin del mercado se realiza mediante variables; para el caso de la segmentacin industrial, se tendrn en cuenta las siguientes:
      
      .row.justify-content-center.align-items-center.mb-5(data-aos="fade-left")
        .col-lg-1.col-4.col-sm-3
          img(src="@/assets/curso/temas/tema2/ico3.svg", alt="")
        .col-lg-11
          h5 Ubicacin geogrfica
          p Si bien, ya se precis el universo del segmento del mercado, es importante definir en qu reas de esa zona estn ubicados los clientes. Por ejemplo, si el universo es Colombia, en este caso se puede detallar como ubicacin geogrfica, las capitales de los departamentos.

        .col-lg-1.col-4.col-sm-3
          img(src="@/assets/curso/temas/tema2/ico4.svg", alt="")
        .col-lg-11
          h5 Tamao de la empresa
          p De acuerdo con el Ministerio de Comercio, Industria y Turismo, en Colombia existen cuatro tamaos de empresas, definidos por su actividad econmica y por los ingresos que generan: 

