      p Esta variable determina a qu se dedican las empresas que hacen parte del mercado objetivo; por ejemplo: panaderas, ferreteras, empresas de servicios de aseo, etc.
      
      .row.justify-content-center.align-items-center.mb-5
        .col-lg-6
          img.imgsize1(src="@/assets/curso/temas/tema2/dane.png").py-3
          p Una vez se hayan aplicado estas variables, es necesario determinar el tamao del mercado de forma cuantitativa; para ello, se puede acceder a informacin secundaria disponible en algunas entidades y agremiaciones; por ejemplo, el DANE actualiza de forma permanente el censo econmico y esta informacin se puede descargar de su sitio web. 
        .col-lg-6
          img.imgsize2(src="@/assets/curso/temas/tema2/fenalco.png").py-3
          p De igual manera, Fenalco proporciona bases de datos de comerciantes y las cmaras de comercio tienen informacin de las empresas registradas en cada ciudad o departamento; alguna tiene costo y otra se puede obtener de forma gratuita. Por consiguiente, la informacin cuantitativa da la posibilidad de crear estrategias con datos precisos y con el mnimo de riesgo en su desarrollo.
      
      hr
       
      .row.justify-content-start.mb-4(data-aos="zoom-in-left")
        .col-sm-12.col-lg-7
          .d-flex.align-items-center.mt-4
            img.w-card-icon(src="@/assets/curso/temas/tema2/4.png")
            h3.bg13.py-2.w-card-text Segmentacin de consumidores
      
      .row.justify-content-center.align-items-center.mb-5(data-aos="zoom-in-left")
        .col-lg-12
          p Es una de las herramientas ms importantes para cualquier empresa, en el desarrollo de estrategias de comercializacin, mercadeo y promocin. La caracterizacin de los clientes permite la satisfaccin de las necesidades con el mnimo de fallas, pues conocer a detalle sus expectativas, se convierte en la informacin ms importante en la generacin de productos y servicios. Esta segmentacin se realiza aplicando variables demogrficas en dos fases diferentes, en la primera se determina el mercado potencial de la empresa y en la segunda el mercado real. Ambas, representan un alto nivel de importancia, debido a las posibilidades que otorgan para el desarrollo de las estrategias.
      
      .row.justify-content-center.align-items-center
        .col-lg-4
          .titulo-sexto.color-acento-contenido
            h5 Figura 1.
            span Proceso de segmentacin
      
      .row.justify-content-center.align-items-center.mb-5
        .col-lg-6
          img(src='@/assets/curso/temas/tema2/figura1.svg', alt='La imagen ilustra el proceso de segmentacin de mercado mediante un diagrama concntrico que abarca desde el universo total hasta segmentos especficos. Comienza con el mercado potencial, definido por variables demogrficas bsicas y geogrficas, y se estrecha hacia el mercado real, caracterizado por variables psicogrficas y demogrficas ms detalladas. Dentro del mercado real, se identifican grupos especficos (Grupo 1, Grupo 2, Grupo 3) que representan segmentos de mercado precisos basados en comportamientos de compra y rangos demogrficos reducidos.')
          figcaption Fuente: SENA (2020)   
      .row.mb-5
        .col-lg-8
          ol.lista-ol--cuadro
            li 
              .lista-ol--cuadro__vineta
                span a
              | <b>Mercado potencial</b>
        .col-lg-12
          p Es el grupo de los posibles consumidores del producto o usuarios del servicio, en este segmento se aplican variables demogrficas en las que se asignan rangos amplios, debido a que se tendrn en cuenta todos aquellos que compran productos similares o de la competencia. Por ejemplo, para una empresa que comercializa caf instantneo, los consumidores de caf en general, hacen parte de su mercado potencial. Este mercado, permite el diseo de estrategias de desarrollo, las cuales se aplican de acuerdo con el anlisis derivado de la matriz de Ansoff, de la que se habl anteriormente. 
          p Variables para la caracterizacin del mercado potencial:
      
      
      .row.justify-content-center.align-items-center.mb-5(data-aos="zoom-in-left")
        .col-lg-3.col-sm-6.col-7
          img(src="@/assets/curso/temas/tema2/img06.png")
          .py-2
        .col-lg-9
