            .row(titulo="Ubicacin geogrfica")
              .col-md-10.mb-4.mb-md-0
                p De la misma forma como se determin el segmento industrial, en el de los consumidores tambin es necesario identificar un rea geogrfica donde la empresa desarrollar sus operaciones, este punto determinar el primer aspecto de potencialidad para su desarrollo comercial. Por ello, una vez definido el universo, es necesario hacerlo con las reas especficas en que se llegar a los consumidores potenciales, debido a que en muchas ocasiones la cobertura total del universo es inalcanzable para la empresa. Por ejemplo, si se defini Antioquia como universo, la ubicacin geogrfica del mercado potencial puede abarcar los municipios del centro y del oriente del departamento.
              .col-md-2.mb-4.mb-md-0.col-4
                img(src="@/assets/curso/temas/tema2/01.png")
            .row(titulo="Sexo")
              .col-md-10.mb-4.mb-md-0
                p Esta variable define el sexo del mercado al que la empresa dirige el producto, muchos de ellos van direccionados a ambos (hombres y mujeres).
              .col-md-2.mb-4.mb-md-0.col-4
                img(src="@/assets/curso/temas/tema2/02.png")
            .row(titulo="Edad")
              .col-md-10.mb-4.mb-md-0
                p En el mercado potencial, como ya se explic, se deben incluir rangos amplios, incluyendo a todos aquellos posibles consumidores.
              .col-md-2.mb-4.mb-md-0.col-4
                img(src="@/assets/curso/temas/tema2/03.png")
            .row(titulo="Nivel socioeconmico")
              .col-md-10.mb-4.mb-md-0
                p Para el caso de Colombia, se determina a travs de la estratificacin que va de 1 a 6. En este caso, tambin se incluyen todos los posibles compradores y por ello, se aplican rangos amplios.
              .col-md-2.mb-4.mb-md-0.col-4
                img(src="@/assets/curso/temas/tema2/04.png")

        p Estas cuatro variables permiten conocer el total del mercado potencial de la empresa; adems, este dato cuantitativo permite planear estrategias de ventas y proyecciones de mercadeo. Por este motivo, al momento de querer cuantificar el mercado, es indispensable acudir a los datos que proporciona el DANE.


