            p Este segmento de mercado incluye a los compradores actuales de un producto o usuarios de un servicio que ya est en el mercado. Para el caso de uno nuevo, incluye los nichos a los que la empresa va a dirigir el producto inicialmente, teniendo en cuenta la investigacin realizada. Con la identificacin del mercado real comienza el proceso publicitario y los datos que de este segmento se derivan, guan las primeras configuraciones del mensaje que finalmente se ver en medios. Para identificar claramente al consumidor con el producto, es importante que las caractersticas de los modelos de los anuncios tengan informacin muy precisa del mercado.
            p Variables para la caracterizacin del mercado real.
            p La caracterizacin de este segmento se hace aplicando las variables demogrficas, esto permite que su identificacin sea ms objetiva gracias a que las variables a utilizar son medibles y pueden generar datos estadsticos muy tiles para el diseo de estrategias:
